.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.ul{
  list-style: none;
}

label {
  color: #02012e;
  font-weight: 500;
}

input[type="submit" i]{
  width: 79px;
  height: 33px;
  background-color: #02012e;
  color: white;
}

.submit{
  margin-top: 23px;
}

.customer,.BranchManager{
  margin-top: 4rem;
}

#password,#pincode{
  margin-left: 30px;
}

input[type="text"] {
  margin-left: 6px;
}

.notifications{
margin-left: 56rem;
background-color: aliceblue;
}

.notificationsmain{
  height: 20rem;
  overflow-y: auto;
  margin-right: 18px;

}

.mainBranch{
  display: flex ;
}

